import * as actionTypes from "./types";
import ApiServiceAuth from "../_services/ApiService/ApiServiceAuth";
import { timeout } from "../_utils/Misc";

const apiServiceAuth = new ApiServiceAuth();

const loginReq = () => ({ type: actionTypes.LOGIN_REQ });

const loginSuccess = () => ({
  type: actionTypes.LOGIN_SUCCESS
});

const loginError = data => ({
  type: actionTypes.LOGIN_ERROR,
  payload: data
});

export const login = userData => {
  return dispatch => {
    dispatch(loginReq());
    apiServiceAuth
      .login(userData)
      .then(r => {
        console.log(r);
        if (r.data && r.data.token) {
          localStorage.setItem("token", r.data.token);
          dispatch(loginSuccess());
        } else {
          dispatch(loginError("Something went wrong in login"));
        }
      })
      .catch(err => {
        dispatch(loginError("Something went wrong in login"));
        timeout(() => dispatch(loginError(null)));
      });
  };
};

export const autoLogin = () => {
  return dispatch => {
    if (localStorage.getItem("token")) dispatch(loginSuccess());
    else dispatch(logoutAction());
  };
};

const logoutAction = () => ({
  type: actionTypes.LOGOUT
});

export const logout = () => {
  return dispatch => {
    localStorage.removeItem("token");
    dispatch(logoutAction());
  };
};
