import React from "react";
import styled from "styled-components";

export const InputBox = ({ label, className, name, ...props }) => {
  return (
    <div className={`${className} Input`}>
      <input type="text" {...props} name={name} />
      <label htmlFor={name}>{label}</label>
      <span class="underline"></span>
    </div>
  );
};

export const AnimatedInput = styled(InputBox)`
  position: relative;
  width: 100%;
  input {
    width: 100%;
    height: 4rem;
    box-sizing: border-box;
    border: 0;
    background: none;
    border-bottom: 1px solid #ccc;
    outline: none;
    border-radius: 0;
    font-size: 1rem;
    -webkit-appearance: none;
    &:focus {
      & ~ label {
        color: ${props => props.theme.colors.accent};
        transform: translateY(-1.5rem);
        font-size: 0.825em;
        cursor: default;
      }
    }
    &:focus {
      & ~ .underline {
        width: 100%;
      }
    }
  }
  label {
    position: absolute;
    top: 0;
    left: 0;
    /* height: 1rem; */
    line-height: 4rem;
    color: #ccc;
    cursor: text;
    transition: all 200ms ease-out;
    z-index: -1;
  }
  .underline {
    content: "";
    display: block;
    position: absolute;
    bottom: -1px;
    left: 0;
    width: 0;
    height: 2px;
    background: ${props => props.theme.colors.accent};
    transition: all 200ms ease-out;
  }
`;
