import React from "react";
import { MainContainer, SpinBtn, Title, Btn } from "../ui/elements";
import { TextField } from "@material-ui/core";
import styled from "styled-components";
import { useForm } from "react-hook-form";
import { connect } from "react-redux";
import { login } from "../../_actions/authActions";

const LoginPage = ({ className, login, auth }) => {
  const { register, handleSubmit, errors } = useForm();
  const onSubmit = data => {
    console.log(auth);
    login(data);
  };

  return (
    <MainContainer className={`LoginPage ${className}`}>
      <Title xl ttu className="Logo">
        Loyaltip
      </Title>
      <form onSubmit={handleSubmit(onSubmit)}>
        <TextField
          label="Email"
          variant="outlined"
          fullWidth
          className="inputField"
          name="email"
          inputRef={register({ required: true })}
          error={!!errors.email}
        />
        <TextField
          error={!!errors.password}
          inputRef={register({ required: true })}
          label="Пароль"
          variant="outlined"
          type="password"
          fullWidth
          name="password"
          className="inputField"
        />
        {auth.error && (
          <Title xs color="danger">
            {auth.error}
          </Title>
        )}

        <SpinBtn
          bgColor="primary"
          fullWidth
          type="submit"
          loading={auth.loading}
        >
          Войти
        </SpinBtn>
      </form>
      <Title l color="primary">
        Регистрация
      </Title>
      <Title s color="primary">
        забыли пароль
      </Title>
    </MainContainer>
  );
};

export default styled(
  connect(state => ({ auth: state.auth }), { login })(LoginPage)
)`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  ${Title} {
    margin-bottom: 2rem;
  }
  .Logo {
    margin-bottom: 5rem;
  }
  ${Btn} {
    margin-bottom: 2rem;
    margin-top: 1rem;
  }
  .inputField {
    margin-bottom: 1rem;
  }
`;
